import {Component, NgZone, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {AngularFirestore} from '@angular/fire/firestore';
import {ICustomWindow, WindowRefService} from '../window-ref.service';
import {CookieService} from 'ngx-cookie-service';
import {HttpClient} from '@angular/common/http';
import {environment} from '../../environments/environment';

interface UserPlan {
  userPlanId: string;
  displayAmount: number;
  duration: number;
  features: Array<string>;
  pricing: number;
  title: string;
}

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.less']
})
export class HomeComponent implements OnInit {

  public userId: string;
  public emailId: string;
  public paymentId: string;
  public userPlanList: UserPlan;
  public parentPlanId: string;
  public gstAmt: number;

  // rzor pay starts here
  public rzp: any;
  // tslint:disable-next-line:variable-name
  private _window: ICustomWindow;

  // rzor pay starts ends here
  constructor(private route: ActivatedRoute,
              public afStore: AngularFirestore,
              private zone: NgZone,
              private winRef: WindowRefService,
              private cookieService: CookieService,
              private http: HttpClient) {
    this.http = http;
  }

  ngOnInit(): void {
    this.userId = this.route.snapshot.paramMap.get('userid');
    this.emailId = this.route.snapshot.paramMap.get('email');

    this.getGstValue().then(r => {
      this.gstAmt = parseInt(r['gstShare']);
    });

    this.userPlanSet().then(r => {
      this.userPlanList = r;
    });

    this.getUserData();
  }

  async userPlanSet() {
    return this.http.post<UserPlan>(environment.merchantServer + 'userPlans/getAllPaymentPlans/', {}).toPromise();
  }

  async addCheckUser(planId) {
    await this.http.post(environment.merchantServer + 'userManagement/confirmPayment/', {
      emailId: this.emailId,
      paymentId: this.paymentId,
      userPlanId: planId,
      paymentSuccess: true
    }).toPromise();

    await this.redirectToApp();
  }

  redirectToApp() {
    window.location.href = 'https://glimpsevc.com/b';
  }

  getUserData() {
    const posts = this.afStore.doc(`users/${this.emailId}`);
    const data = this.regUser().then(r => {
    });
  }

  regUser() {
    return this.http.post(environment.merchantServer + 'userManagement/registerUser/', {
      emailId: this.emailId
    }).toPromise();
  }

  doPayment(amount, planId): void {
    this.parentPlanId = planId;
    this.rzp = new this.winRef.nativeWindow['Razorpay']({
      key: 'rzp_live_aIOxiqLOFaYWYS',
      name: 'Glimpse',
      description: 'Subscription Fees',
      amount: (amount * 100) + ((amount * 100) * (this.gstAmt / 100)),
      prefill: {
        name: 'Glimpse'
      },
      theme: {
        color: '#3880FF',
      },
      handler: this.paymentHandler.bind(this),
      modal: {
        ondismiss: () => {
          this.zone.run(() => {
            // add routing if it fails
          });
        },
      },
    });
    this.rzp.open();
  }

  getGstValue() {
    return this.http.post(environment.merchantServer + 'getStatic/', {
      keys: ['gstShare']
    }).toPromise();
  }

  paymentHandler(res: any) {
    this.paymentId = res.razorpay_payment_id;
    this.addCheckUser(this.parentPlanId);
    this.zone.run(() => {
      // add API call here
    });
  }
}
